.message {
  align-self: stretch;
}
.message,
.text {
  position: relative;
  line-height: 30px;
}
.search {
  align-self: stretch;
  background-color: var(--neutral-light-3);
  border: 1px solid var(--color-lightsteelblue);
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-lg);
}
.box,
.search,
.wireframeInput {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex: 1;
}
.box {
  align-self: stretch;
  border-radius: var(--br-5xs);
  border: 1px solid var(--neutral-2);
  overflow: hidden;
  flex-direction: row;
  color: var(--color-lightsteelblue);
}
.wireframeInput {
  width: 708px;
  flex-direction: column;
  gap: var(--gap-5xs);
  text-align: left;
  font-size: var(--body-midd-size);
  color: var(--neutral-2);
  font-family: var(--h4);
}
